import { useCallback, useMemo, useState } from "react";

type GetRetailersParams = {
    centerId: string | number,
    apiURI?: string,
    limit?: number,
    page?: number,
}

type Data = {
    isError: boolean,
    error: string | null,
    items: any[],
    count: number,
}

const defaultData: Data = {
    isError: false,
    error: null,
    items: [],
    count: 0,
}
const useGetRetailersBySearch = (params: GetRetailersParams) => {
    const { centerId, apiURI, limit, page } = params;

    const fetchRetailers = useCallback((search: string) => {
        const r = /\/$/;
        const uri = apiURI?.replace(r, '');
        const url = `${uri}/v1/retailers/?limit=${limit ? limit : 100}&page=${page ? page : 1}&search=${search ? search : ''}`;

        return fetch(url, {
            headers: {
                center_id: centerId.toString(),
            },
        }).then(response => {
            if (!response.ok) {
                return {
                    ...defaultData,
                    isError: true,
                    error: `Server error: [${response.status}] [${response.statusText}] [${response.url}]`,
                    items: [],
                };
            }
            return (async () => {
                return await response.json().then(retailers => {
                    return {
                        ...defaultData,
                        items: retailers.items,
                        count: retailers.count,
                    }
                });
            })()
        }).catch((e: any) => {
            return {
                ...defaultData,
                isError: true,
                error: e.message ? e.message : 'useGetRetailer: something went wrong!',
                items: [],
            };
        })
    }
        , [centerId, limit, page, apiURI]);

    return fetchRetailers;
}

export default useGetRetailersBySearch;
