import { Col, Form, FormGroup, Modal, Placeholder, Row, Spinner  } from "react-bootstrap";
import { XCircle } from "react-feather";
import { XCircleFill } from "react-bootstrap-icons";
import { IRetailer, MapObj } from "src/mapitApiTypes";
import { CSSProperties, ChangeEventHandler, MouseEventHandler, useMemo, useState } from "react";
import useRetailerData from "src/Hooks/useRetailerData/useRetailerData";
import styles from './RetailerInfoModal.module.css';

type RetailerInfoModalProps = {
    amenity: string,
    show: boolean,
    onHide: () => void,
    centerId: string,
    retailers?: IRetailer[],
    mapObjs?: MapObj[],
    mapObjectName?: string,
    webApiURI: string,
    onClick?: (id: string, amenityType?: string) => void,
}

const RetailerInfoList = ({retailers, retailer, isLoading, onClick, isAmenity, mapObjs}: {retailers?: IRetailer[], retailer: IRetailer | null, isLoading: boolean, onClick?: (id: string) => void, isAmenity: boolean, mapObjs?: MapObj[]}) => {

    const [retailerId, setRetailerId] = useState<string>('');

    const handleOnClick: MouseEventHandler<HTMLButtonElement> = (e) => {
        e.preventDefault();
        if (onClick) {
            onClick(retailerId);
        }
    }

    const onChange: ChangeEventHandler<HTMLSelectElement> = (e) => {
        setRetailerId(e.target.value);
    }

    const filterRetailers = (retailers:IRetailer[], arrayObjects:MapObj[]) => {
        return retailers.filter(retailer => 
            arrayObjects.some(obj => obj.retailer_id === retailer.id)
        );
    }

    if (!isAmenity && (isLoading || !retailer )) {
        return (
        <div>
            <div className={styles['retailer-info-spinner']} >
                <Spinner animation="border" role="status" variant="secondary"/>
            </div>
            <div className={styles['retailer-info-content']} >
                <div>
                    <Placeholder bg="secondary"  xs={4} /> <Placeholder bg="secondary"  xs={5} /> <Placeholder bg="secondary"  xs={3} />
                </div>
                <div>
                    <Placeholder bg="secondary"  xs={4} /> <Placeholder bg="secondary"  xs={3} /> <Placeholder bg="secondary"  xs={2} /> <Placeholder bg="secondary"  xs={5} />
                </div>
                <div>
                    <Placeholder bg="secondary"  xs={3} /> <Placeholder bg="secondary"  xs={7} />
                </div>
                <div>
                    <Placeholder bg="secondary"  xs={4} /> <Placeholder bg="secondary"  xs={3} /> <Placeholder bg="secondary"  xs={2} /> <Placeholder bg="secondary"  xs={5} />
                    <Placeholder bg="secondary"  xs={4} /> <Placeholder bg="secondary"  xs={3} /> <Placeholder bg="secondary"  xs={2} /> <Placeholder bg="secondary"  xs={5} />
                </div>
            </div>
        </div>
        )
    }

    return (
        <div>
            {!!retailer && (
                <>
                    <div className={styles['retailer-logo']}>
                        <img className={styles['img']} src={retailer.logo} alt={retailer.retail_name} />
                    </div>
                    <div className={styles['retailer-info-content']} >
                        <div>
                            <b>Retailer:</b> {retailer.retail_name}
                        </div>
                        {retailer.location &&
                            <div>
                                <b>Location:</b> {retailer.location}
                            </div>
                        }
                        {retailer.retailer_phone &&
                            <div>
                                <b>Phone:</b> {retailer.retailer_phone}
                            </div>
                        }
                        {retailer.retailer_description &&
                            <div dangerouslySetInnerHTML={{ __html: retailer.retailer_description }}></div>
                        }
                    </div>
                </>
            )}
            <Form className={styles['get-directions-from']}>
                <FormGroup>
                    <div className="d-flex align-items-center gap-3">
                        <div className="label text-nowrap">Get directions from</div>
                        <Form.Select
                            name="retailer"
                            onChange={onChange}
                            value={retailerId}
                            >
                            <option value={''} selected disabled>Choose Retailer ...</option>
                            {filterRetailers(retailers ?? [], mapObjs ?? [])?.map((item, index) => {
                                if (retailer && item.id === retailer.id) return null;
                                return (
                                    <option key={index} value={item.id}>{item.retail_name}</option>
                                )
                            }
                            )}
                        </Form.Select>
                        <button onClick={handleOnClick} className="btn btn-primary text-nowrap">Map IT</button>
                    </div>
                </FormGroup>
            </Form>
        </div>
    );
}

const RetailerInfoModal = (props: RetailerInfoModalProps) => {
    const {
        amenity,
        show,
        onHide,
        centerId,
        retailers,
        mapObjs,
        mapObjectName,
        webApiURI,
        onClick,
    } = props;
    const slug = useMemo(() => {
        if (centerId && mapObjs && retailers && mapObjectName) {
            const mapObj = mapObjs.find(item => item.map_obj_name === mapObjectName);
            if (mapObj && mapObj.retailer_id) {
                const retailer = retailers.find(item => item.id === mapObj.retailer_id);
                if (retailer) {
                    return retailer.slug;
                }
            }
        }
        return '';
    }, [centerId, mapObjectName, mapObjs, retailers]);
    const data = useRetailerData({ centerId, slug, webApiURI });
    const handleOnClick = (retailerId: string) => {
        if (!onClick) return;
        const mapObj = mapObjs?.find(item => parseInt(item.retailer_id) === parseInt(retailerId));
        let mapObjectName = '';

        if (mapObj) {
            mapObjectName = mapObj.map_obj_name;
        }
        if (amenity) {
            onClick(mapObjectName, amenity);
        } else {
            onClick(mapObjectName);
        }
    }

    const isAmenity = (amenity !== '' || (mapObjectName? mapObjectName.startsWith('kiosk') : false)) && !(mapObjectName && (mapObjectName.startsWith('store') || mapObjectName.startsWith('big-store')));
    return (
        <Modal show={show} dialogClassName={styles['retailer-info-modal']} className="px-2" onHide={onHide} centered>
            <div className={styles['retailerInfoCloseBtn']}>
                <XCircle onClick={onHide} size={24} color="white" className={styles['close-x-circle']} />
            </div>
            <Modal.Body className="p-3">
                <RetailerInfoList retailers={retailers} retailer={data.data} isLoading={data.isLoading} onClick={handleOnClick} isAmenity={isAmenity} mapObjs={mapObjs} />
            </Modal.Body>
        </Modal>
    )
}

export default RetailerInfoModal;
